import React from 'react'
import { Link } from 'react-router-dom'
function Navbar() {
    return (
        <nav className="navbar navbar-expand-lg bg-body-tertiary fixed-top mb-5">
            <div className="container-fluid">
                <Link className="navbar-brand" to=''>logo</Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon" />
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                        <li className="nav-item me-2">
                            <Link className="nav-link active" aria-current="page" to="">Home</Link>
                        </li>
                        <li className="nav-item me-2">
                            <Link className="nav-link active" to="/productscrud">Crud operations in products</Link>
                        </li>
                        <li className="nav-item me-2">
                            <Link className="nav-link active" to="/catigorize">Products</Link>
                        </li>
                        <li className="nav-item me-2">
                            <Link className="nav-link active" to="/cart">Cart</Link>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
    )
}

export default Navbar